<script>
import lottie from "lottie-web";
import clapping from "@/assets/clapping.json";

import { colorify } from "lottie-colorify";

export default {
  name: "Welcome",
  data() {
    return {
      animation: null,
      scrollTopPercentAtWelcome: 0,
      scrollBoundaryTop: 3.15,
      scrollBoundaryBottom: 4.25,
      colorLists: [
        "#3AA6B9",
        "#FF7D29",
        "#FF9EAA",
        "#A1DD70",
        "#FFA27F",
        "#FF0000",
        "#D2649A",
        "#CAE6B2",
        "#F6FAB9",
        "#6DC5D1",
        "#FDE49E",
        "#DD761C",
        "#FFF9D0",
        "#C73659",
        "#5AB2FF",
        "#0A6847",
        "#FFC470",
        "#B51B75",
        "#FF76CE",
        "#B5C18E",
        "#FFB1B1",
        "#BED7DC",
        "#D20062",
        "#FA7070",
        "#90D26D",
        "#FF5BAE",
        "#FFE4CF",
        "#FFFDD7",
        "#A8CD9F",
        "#E2F4C5",
      ],
      colorIndices: [
        0, 1, 0, 1, 0, 0, 0, 0, 2, 0, 2, 0, 1, 0, 3, 0, 3, 0, 0, 0, 0, 2, 0, 2,
        0, 3, 0, 4, 0, 4, 0, 0, 0, 0, 2, 0, 2, 0, 4,
      ], // for this specific animation, there are 5 colors and here we indicate the order of the colors
    };
  },
  computed: {},
  methods: {
    onScrollWelcome() {
      this.scrollTopPercentAtWelcome =
        window.scrollY / window.innerHeight;
    },
    async loadAnimation() {
      console.log("loading animation");
      let me = this;

      for (let i = 0; i < 33; i++) {
        setTimeout(() => {
          lottie.loadAnimation({
            container: document.getElementById("animationContainer" + i),
            loop: true,
            autoplay: true,
            animationData: colorify(
              me.generateColorListBasedOnIndex(i),
              clapping,
            ),
          });
        }, 1000 * Math.random());
      }
    },
    generateColorListBasedOnIndex() {
      let colors = [];
      // pick three colors from the list
      // reorder the colorlist and pick 3
      this.colorLists = this.colorLists.sort(() => Math.random() - 0.5);
      let pickedColors = this.colorLists.slice(0, 3);
      for (let i = 0; i < this.colorIndices.length; i++) {
        if (this.colorIndices[i] == 0) {
          colors.push("#000000");
        } else if (this.colorIndices[i] == 2) {
          colors.push("#ffffff");
        } else if (this.colorIndices[i] == 1) {
          colors.push(pickedColors[0]);
        } else if (this.colorIndices[i] == 3 || this.colorIndices[i] == 4) {
          colors.push(pickedColors[this.colorIndices[i] - 2]);
        }
      }
      return colors;
    },
  },
  async mounted() {
    this.scrollTopPercentAtWelcome =
      window.scrollY / window.innerHeight;
    await this.loadAnimation();
    window.addEventListener("scroll", this.onScrollWelcome);
  },

  beforeDestroy() {
    window.removeEventListener("scroll", this.onScrollWelcome);
  },
};
</script>

<style>
.welcome-text path {
  fill: #0d293e;
  stroke: #fffdb5;
  stroke-dasharray: 50;
  animation: draw-stroke 5s infinite;
}

@keyframes draw-stroke {
  0% {
    stroke-dashoffset: 0;
    stroke-width: 1px;
  }
  50% {
    stroke-dashoffset: 50;
    stroke-width: 0px;
  }
  100% {
    stroke-dashoffset: 0;
    stroke-width: 1px;
  }
}
</style>
